/* label.css */
.storybook-label {
    font-family: "Work Sans", sans-serif;
    font-weight: 500;
    color: #111827;
    display: inline-block;
    margin-bottom: 4px;
    cursor: default;
}

.storybook-label--small {
    font-size: 12px;
}

.storybook-label--medium {
    font-size: 14px;
}

.storybook-label--large {
    font-size: 16px;
}

.storybook-label--primary {
    color: #011e58;
}

.storybook-label--secondary {
    color: #6B7280;
}